import * as React from 'react';
import { Flex, Heading, Box, Text, Badge } from '@chakra-ui/core';
import { formatDate, nl2br } from '~/utils';
import SectionTitle from '../SectionTitle';
import Section from '../Section';

export default function Promo({ data, ...props }) {
  return (
    <Section {...props}>
      <Box mx={[2, 8, 16]}>
        <SectionTitle>Promozioni</SectionTitle>
        {data.map((item) => {
          const description = nl2br(item.descrizione);
          const scadenza = formatDate(item.scadenza);

          return (
            <Box p={4} key={item.scadenza} as="article" minHeight="12em">
              <Flex justifyContent="space-between">
                <Heading size="xs" mb={1}>
                  {item.titolo}
                </Heading>
                <Badge variantColor="purple">{item.area}</Badge>
              </Flex>
              <Flex
                borderRadius="50%"
                w="92px"
                h="92px"
                backgroundColor="green.100"
                align="center"
                justify="center"
                float="left"
                m={2}
                style={{ shapeOutside: 'circle()' }}
              >
                <img
                  src={`/static/images/partners/${item.produttore.toLowerCase()}.png`}
                  width="92px"
                  alt={item.produttore}
                  loading="lazy"
                />
              </Flex>
              <Text fontSize="sm" dangerouslySetInnerHTML={{ __html: description }} />
              <Text fontSize="sm" fontWeight={100}>
                Valida fino al {scadenza}
              </Text>
            </Box>
          );
        })}
      </Box>
    </Section>
  );
}
